import React, { useState, useEffect } from "react"
import { CSSTransition } from "react-transition-group"

import "../styles/intro.scss"

const Intro = () => {
  const [showModal, closeModal] = useState(
    (typeof window !== `undefined`)
      ? (window.localStorage.getItem('intro') || 'show')
      : 'show'
  )

  useEffect(() => {
    if (typeof window !== `undefined`) {
      window.localStorage.setItem('intro', 'hide')
    }
    if (typeof document !== `undefined`) {
      document.body.style.overflowY = ((showModal === 'hide'/*'show'*/) ? 'hidden' : 'visible')
    }
  })

  return (
    <>
      <CSSTransition
        appear={true}
        in={showModal === 'hide'/*'show'*/}
        timeout={100}
      >

        <div className={`modal-background`}>
          <div className={`modal-content`}>
            <h1 className={`intro-header`}>{'Infinite Scroll Is Not Enough'}</h1>

            <div className={`intro-body`}>
              <p>{`Contributors: Jonathan, Xuejin, Benjaim, Ka Ming`}</p>
              <p>{`Welcome to our app!`}</p>
              {``}
            </div>
            
            <button className={`close-modal`} onClick={() => closeModal('show'/*'hide'*/)}>{`Close`}</button>
          </div>
        </div>

      </CSSTransition>
    </>
  )
}

export default Intro
